@charset "UTF-8";
/*  tamaños para media queries (desde este tamaño en adelante, ej: @media (min-width: $tablet) */
/* fuente principal */
/* colores */
/* Reseteamos los margin y paddings de todas las etiquetas */
*, *::before, *::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

*::before, *::after {
  display: block;
}

/* Evitamos problemas con las imagenes */
img, picture, video, iframe, figure {
  display: block;
  max-width: 100%;
  width: 100%;
  /* (Opcional) */
  object-fit: cover;
  /* (Opcional) */
  object-position: center center;
}

/* Reseteamos los enlaces para funcionar como cajas... */
a {
  display: block;
  cursor: pointer;
}

/* ... excepto los que se encuentran en párrafos */
p a {
  display: inline;
}

/* Quitamos los puntos de los <li> */
li {
  list-style-type: none;
}

/* (Opcional) Configuramos anclas suaves */
html {
  scroll-behavior: smooth;
}

/* Desactivamos estilos por defecto de las principales etiquetas de texto */
h1, h2, h3, h4, h5, h6, p, span, a, strong, blockquote, i, b, u, em {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  text-transform: none;
  color: inherit;
}

/* Evitamos problemas con los pseudoelementos de quotes */
blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

/* (Opcional) Configuramos el texto que seleccionamos */
::selection {
  background-color: #000;
  color: #fff;
}

/* Nivelamos problemas de tipografías y colocación de formularios */
form, input, textarea, select, button, label {
  font-family: inherit;
  font-size: inherit;
  hyphens: auto;
  background-color: transparent;
  color: inherit;
  display: block;
  /* (Opcional) */
  appearance: none;
}

/* Reseteamos las tablas */
table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Evitamos problemas con los SVG */
svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

/* Configuramos la tipografía para toda la web */
body {
  min-height: 100vh;
  font-size: 100%;
  /* /Opcional) */
  line-height: 1.4em;
  /* (Opcional) */
  -webkit-font-smoothing: antialiased;
  /* (Opcional) */
  -moz-osx-font-smoothing: grayscale;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

h1, h2, h3, h4 {
  font-weight: 600;
  text-align: center;
  font-family: Verdana;
}

h4 {
  font-weight: normal;
}

body {
  max-width: 112.5rem;
  font-family: Helvetica Arial sans-serif;
  color: #000;
  background-color: #fff;
}

.o-container {
  display: block;
  width: 100%;
  height: auto;
  margin: 0 auto;
}

.c-separador {
  height: 200px;
}

.c-banner1 {
  margin: 3rem 0;
}
.c-banner1__contenedor {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border: 2px solid #fff;
  border-radius: 0.5rem;
  box-shadow: 0.5rem 0.5rem 1rem #181818, -0.5rem -0.5rem 1rem #181818;
  transition: all 1s ease;
}
@media (min-width: 768px) {
  .c-banner1__contenedor {
    width: 80%;
  }
}
.c-banner1__contenedor-arriba {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  padding: 0.5rem;
}
@media (min-width: 768px) {
  .c-banner1__contenedor-arriba {
    padding: 1rem;
  }
}
.c-banner1__contenedor-abajo {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0.5rem;
}
@media (min-width: 768px) {
  .c-banner1__contenedor-abajo {
    padding: 1rem;
  }
}
.c-banner1__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.5rem;
}
@media (min-width: 480px) {
  .c-banner1__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5rem;
    padding: 0.5rem;
  }
}
@media (min-width: 960px) {
  .c-banner1__grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 0.5rem;
  }
}
.c-banner1__producto {
  max-width: 80px;
  cursor: pointer;
  transition: transform 0.7s ease;
}
@media (min-width: 480px) {
  .c-banner1__producto {
    max-width: 100px;
  }
}
@media (min-width: 768px) {
  .c-banner1__producto {
    max-width: 120px;
  }
}
@media (min-width: 960px) {
  .c-banner1__producto {
    max-width: 150px;
  }
}
.c-banner1__producto:hover {
  transform: scale(1.1);
}
.c-banner1__texto {
  font-size: 1.2em;
  width: 40%;
}
@media (min-width: 768px) {
  .c-banner1__texto {
    width: 20%;
  }
}
.c-banner1__texto p {
  margin: 0 auto;
  text-transform: uppercase;
  font-size: 0.8em;
}
@media (min-width: 768px) {
  .c-banner1__texto p {
    font-size: 1em;
  }
}
.c-banner1__logo {
  max-width: 10em;
  margin-right: 0.5rem;
}
@media (min-width: 768px) {
  .c-banner1__logo {
    margin-right: 2rem;
  }
}
.c-banner1__nombre {
  font-size: 1em;
  margin-left: 0.5rem;
}
@media (min-width: 768px) {
  .c-banner1__nombre {
    margin-left: 2rem;
    font-size: 1.5em;
  }
}

.c-banner2 {
  margin: 3rem 0;
  display: none;
}
@media (min-width: 768px) {
  .c-banner2 {
    display: block;
  }
}
.c-banner2__contenedor {
  width: 15%;
  padding: 1rem;
  margin-left: 1rem;
  background-color: #fff;
  border: 2px solid #fff;
  border-radius: 0.5rem;
  box-shadow: 0.5rem 0.5rem 1rem #181818, -0.5rem -0.5rem 1rem #181818;
  transition: all 1s ease;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
.c-banner2__logo {
  max-width: 10em;
}
.c-banner2__nombre {
  margin: 1rem 0;
}
.c-banner2__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0.5rem;
}

.u-flex {
  display: flex !important;
}

.u-rotation {
  transform: rotate(180deg);
}